          <ThemedText.DeprecatedMediumHeader style={{ marginLeft: "8px" }}><Trans i18nKey="migrate.lpNFT" values={{ symA: currency0.symbol, symB: currency1.symbol }} /></ThemedText.DeprecatedMediumHeader>
