            <ThemedText.DeprecatedBlack fontSize={14}>{invertPrice ? `${v2SpotPrice?.invert()?.toSignificant(6)} ${currency0.symbol}` : `${v2SpotPrice?.toSignificant(6)} ${currency1.symbol}`}</ThemedText.DeprecatedBlack>
